in other apps and access them on the Import Module screen, just select one of the options below.'\"></StudioInfo>\n        <div>\n            <div class=\"share-type\">\n                <RadioButtonComp class=\"radio-btn\" :active=\"shareType == 'reference'\" :type=\"'preview'\" @click=\"selectShareType('reference')\"/>\n                <span>Reference Module</span>\n            </div>\n            <div class=\"share-type\">\n                <RadioButtonComp class=\"radio-btn\" :active=\"shareType == 'version'\" :type=\"'preview'\" @click=\"selectShareType('version')\"/>\n                <span>Version Module</span>\n            </div>\n        </div>\n        <StudioInfo v-if=\"shareType == 'reference'\" style = \"margin-bottom: 20px;\" :infoText=\"'Your changes will affect all apps using this module.'\"></StudioInfo>\n        <StudioInfo v-else-if=\"shareType == 'version'\" style = \"margin-bottom: 20px;\" :infoText=\"'Your changes will not affect the application unless you change the version number and re-install the module.'\"></StudioInfo>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button class=\"share-button\" @click=\"share()\">SHARE</button>\n        </div>\n        \n    </div>\n</template>\n<style scoped>\n    @import url(\"../../../../../../src/styles/IDE3.css\");\n    \n    .container {\n        padding: 32px 20px 20px;\n    }\n    .share-type {\n        margin-bottom: 20px;\n        display: flex;\n        align-items: center;\n        padding: 0 12px;\n    }\n    .share-type span {\n        color: #212121;\n        font-size: 14px;\n    }\n    .radio-btn {\n        margin-right: 10px;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 52px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-ite